	export default {
		data() {
			return {
				background: {
					backgroundColor: '#fff',
				},
				open: false,
				openPos:false,
			}
		},
		onLoad(e) {
			if (e != undefined) {
				if (e.open == "false") {
					this.open = false
				} else {
					this.open = true
				}

			}
		},

		methods: {
			// switch打开或者关闭时触发，值为true或者false
			// 即使不监听此事件，this.checked此时也会相应的变成true或者false
			//是否开启商城
			change(status) {
				console.log(status)
				this.open = status
			},
			
			//是否开启地域限制
			change2(status){
				this.openPos = status
			},

			goBack() {
				// 获取当前页面栈
				let pages = getCurrentPages()
				// pages数组中的最后一个为当前页面，倒数第二个为上一个页面
				let prevPage = pages[pages.length - 2]

				let data = "";
				if (this.open) {
					data = "true"
				} else {
					data = "false"
				}
				// 重点来了
				// 调用$vm 注册一个自定义方法 将参数传入进去
				prevPage.onShow(data)
				uni.navigateBack({
					delta: 1
				})
			},

            saveSeting(){
				uni.showModal({
					title:"提示",
					content:"是否保存设置",
					confirmColor:"#FF7200",
					success: (res) => {
						if (res.confirm) {
							this.save()
						}
					}
				})
			},
			
			save(){
				
			}
		}
	}
